.home-video {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: -1;

    backface-visibility: hidden;
    transform: translateZ(0);

    pointer-events: none;

    video {
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
        position: absolute;
        left: 0;
        top: 0;
    }

    &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba(white, 0.76);
    }
}

.home-video__background-image {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    @include objectFitCover();
}

.home-video__green-backplate {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(#183029, 0.5);
    z-index: 10;
    opacity: 0;

    &--grey {
        background: white;
    }
}
